<template>
	<div von-app>
		<navbar></navbar>
		<transition name="index">
			<router-view name="index"></router-view>
		</transition>
		<transition name="other">
			<router-view></router-view>
		</transition>
	</div>
</template>
<script>
	import Vue from 'vue'
	import channel from './channel'
	import Navbar from './Navbar'

	Vue.directive('nav', {
		inserted: function(el, binding) {
			let data = binding.value
			channel.$emit('UpdateNavbar', data)
		}
	})

	export default {
		components: {
			Navbar
		},

		data() {
			return {
				gradeClass: 'grade-a'
			}
		},

		created() {
			// grade-a for ios, grade-b for android & other
			if(/iPad|iPhone|iPod/.test(navigator.userAgent)) {
				this.gradeClass = 'grade-a'
			} else {
				this.gradeClass = 'grade-b'
			}

			if(window.__page_transition__ == 'ios') {
				this.gradeClass = 'grade-a'
				window.__disable_nav_title_transition__ = false
			} else if(window.__page_transition__ == 'android') {
				this.gradeClass = 'grade-b'
				window.__disable_nav_title_transition__ = true
			}

			document.querySelector('body').className = this.gradeClass

			channel.$on('VonicNotification', (data) => {
				$toast.show(data.message);
			})
		},

		methods: {

		}
	}
</script>
<style scoped>
	  .index-enter {
 	  	
 	  }
 	  .index-enter-to {
 	  	
 	  }
 	  
 	  .index-leave {
 	  	opacity: 1;
 	  }
 	  
 	  .index-leave-to {
 	  	opacity: 0;
 	  	transition: 0.5s;
 	  }
 	  
 	  
 	  .other-enter {
 	  	position: absolute;
 	  	left: 100%;
 	  	/*transform: scale(0);
 	  	opacity: 0;*/
 	  }
 	  .other-enter-to {
 	  	position: absolute;
 	  	left: 0px;
 	  	transition: 0.2s;
 	  	z-index: 10000;
 	  	/*transform: scale(1);
 	  	opacity: 1;*/
 	  }
 	  
 	  .other-leave {
 	  	position: absolute;
 	  	left: 0px;
 	  	/*transform: scale(1);
 	  	opacity: 1;*/
 	  }
 	  
 	  .other-leave-to {
 	  	position: absolute;
 	  	left: 100%;
 	  	transition: 0.2s;
 	  	z-index: 10000;
 	  	/*transform: scale(0.5);
 	  	opacity: 0;*/
 	  }
</style>


