<template>
  <div class="bar bar-header item-input-inset">
  	<button  class="button back ion-ios-arrow-left" @click="back"></button>
    <label class="item-input-wrapper">
      <i class="icon ion-ios-search-strong placeholder-icon"></i>
      <form action='' :id="formId" >
        <input ref="input" type="search" :placeholder="placeholder" :value="value" @change="updateValue($event.target.value)">
      </form>
    </label>
    <button class="button search" @click='search' v-text="cancelText"></button>
  </div>
</template>
<script>
  export default {
    props: {
      value: {
        type: String,
        required: true
      },
      placeholder: {
        type: String,
        default: 'Search'
      },
      cancelText: {
        type: String,
        default: 'Cancel'
      },
      onSearch: Function,
      onCancel: Function
    },

    data() {
      return {
        formId: 'von-search-' + Math.random().toString(36).substring(3, 6),
        
      }
    },

    mounted() {
      document.getElementById(this.formId).onsubmit = this.search
    },

    methods: {
      search(e) {
        e.preventDefault()
        let search = document.querySelector('#' + this.formId + ' > [type=search]')
        search.blur()
        if (this.onSearch) this.onSearch()
      },

      cancel() {
        if (this.onCancel) this.onCancel()
      },

      updateValue(value) {
        this.$refs.input.value = value,
        this.$emit('input', value)
      },
      back(){
      	this.$router.go(-1)
      }
    }
  }
</script>
<style scoped>
	.item-input-wrapper {
		line-height: 0;
	}
	.item-input-wrapper form {
		width: 100%;
	}
	.item-input-wrapper .icon {
		font-size: 24px;
	}
	.back {
		font-size: 28px;
		background: none;
		margin-right: 5px;
		padding: 0px 15px 0px 5px;
	}
	.search {
		font-size: 15px;
		color: #555;
		background: none;
	}
</style>



